<template>
  <div id="app">
       <b-button @click="addContent()" variant="success">Button</b-button>
  <b-row class="desktop">
    <b-col><m-ace-editor height="300px" v-model="htmlCode" :line-number="True" mode="html" theme="monokai"></m-ace-editor></b-col>
    <b-col><m-ace-editor height="300px" v-model="cssCode" :line-number="True" mode="css" theme="monokai"></m-ace-editor></b-col>
    <div class="w-100"></div>
    <b-col><m-ace-editor height="300px" v-model="jsCode" :line-number="True" mode="javascript" theme="monokai"></m-ace-editor></b-col>
    <b-col><iframe id="finalIframe" width="100%" height="100%"> </iframe></b-col>
  </b-row>
  </div>
 
</template>

<script>
import HelloWorld from "./components/HelloWorld.vue";

export default {
  name: "app",
  components: {
    HelloWorld
  },
  data: function() {
    return {
      htmlCode: "<p>welcome to ash!</p>",
      cssCode: "body{ color: red; background:#fff; }",
      jsCode: "console.log('welcome to ash')"
    };
  },
  methods: {
    addContent() {
      let x = document.getElementById("finalIframe").contentWindow.document;
      x.open();
      console.log(this.htmlCode, this.jsCode);
      x.write(this.htmlCode);
      //console.log(this.jsCode.search('console.log'))
      x.write(`<script>${this.jsCode}<\/script>`);
      x.write(`<style>${this.cssCode}<\/style>`);
      x.close();
    }
  },
  mounted: function() {
    // Overriding console object
    var console = {};

    // Getting div to insert logs
    var logger = document.getElementById("logger");

    // Adding log method from our console object
    console.log = function(text) {
      var element = document.createElement("div");
      var txt = document.createTextNode(text);

      element.appendChild(txt);
      logger.appendChild(element);
    };
    // console.log(44444);
    this.addContent();
  }
};
</script>

